html {
	box-sizing: border-box;
	font-size: 0.052083333333333336vw;

	@media (max-width: 991px) {
		font-size: 0.18vw;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320rem;
	font-family: "GoogleSans", sans-serif;
	font-size: 16rem;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #000;
	transition: all 0.3s ease;

	&:hover {
		color: #2486E8;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

$color-primary: #2486E8;

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15rem;
	max-width: 1370rem;

	@media (max-width: 991px) {
		max-width: 750rem;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-size: 44rem;
	line-height: 56rem;

	@media (max-width: 991px) {
		font-size: 34rem;
		line-height: 40rem;
	}

	i {
		display: block;
		font-style: normal;
		font-weight: 400;
	}

	span {
		font-weight: 500;
		font-size: 63rem;
		color: #2486E8;
		padding-bottom: 20rem;
	}
}

.buttons {
	margin-top: 70rem;
	display: flex;

	@media (max-width: 991px) {
		margin-top: 35rem;
	}
}

.button {
	border: 1rem solid #000;
	border-radius: 5rem;
	background-color: #fff;
	padding: 5rem 20rem;
	display: flex;
	align-items: center;

	&:hover {
		color: #000;
	}

	svg {
		width: 33rem;
		height: 37rem;
	}

	p {
		margin-bottom: 3rem;
		font-size: 12rem;
		line-height: 15px;
		color: #000;
	}

	&__text {
		margin-left: 10rem;

		svg {
			width: 121rem;
			height: 24rem;
		}
	}
}

.button+.button {
	margin-left: 50rem;

	@media (max-width: 991px) {
		margin-left: 25rem;
	}
}