.tabs {
	position: relative;
	z-index: 1;

	@media (max-width: 991px) {
		margin-top: 60rem;
		display: flex;
		flex-direction: column;
	}

	&__list {
		display: flex;
		flex-direction: column;

		@media (min-width: 992px) {
			flex: 0 0 60%;
		}
	}

	&__item {
		border: none;
		cursor: pointer;
		background-color: #F7F9FC;
		border-radius: 8rem;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:hover {
			background-color: $color-primary;

			&:hover .tabs__btn {
				color: #fff;

				&::before {
					filter: brightness(10);
				}

				&::after {
					filter: brightness(10);
				}
			}
		}

		&.active {
			background-color: $color-primary;
			transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
			pointer-events: none;

			.tabs__btn {
				color: #fff;

				&::before {
					filter: brightness(10);
				}

				&::after {
					filter: brightness(10);
				}
			}
		}
	}

	&__item+&__item {
		margin-top: 20rem;
	}

	&__content {
		display: none;
		margin-left: 150rem;

		@media (max-width: 991px) {
			margin: 60rem 0 0 0;
		}

		&.active {
			display: block;
		}
	}

	&__btn {
		position: relative;
		font-weight: 700;
		font-size: 20rem;
		line-height: 145%;
		width: 100%;
		text-align: left;
		padding: 25rem 30rem;

		@media (max-width: 991px){
			padding-right: 90rem;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-right.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 30rem;
			transform: translateY(-50%);
			width: 32rem;
			height: 32rem;
		}

		&::after {
			content: '';
			background-image: url('../img/svg/plus.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 10rem;
			left: 10rem;
			width: 16rem;
			height: 16rem;
		}
	}

	&__image {
		width: 377rem;
		height: 644rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}