.contacts {
	position: relative;

	&__map {
		height: 600rem;

		@media (max-width: 991px) {
			height: 300rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.address-contacts {
	position: absolute;
	top: 50%;
	left: 573rem;
	transform: translateY(-50%);
	background-color: #fff;
	border-radius: 8rem;
	padding: 25rem 35rem 25rem 25rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	@media (max-width: 991px) {
		left: 10rem;
	}

	&::before {
		content: '';
		background-image: url('../img/svg/marker.svg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: -10rem;
		right: -80rem;
		width: 63rem;
		height: 92rem;
	}

	&::after {
		content: '';
		position: absolute;
		top: 50%;
		right: -30rem;
		transform: translateY(-50%);
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 25.5rem 0 25.5rem 51rem;
		border-color: transparent transparent transparent #fff;
	}

	&__item {
		position: relative;
		display: inline-block;
		padding-left: 50rem;
		font-weight: 500;
		font-size: 18rem;
		line-height: 145%;
		color: #000;

		@media (max-width: 991px) {
			font-size: 16rem;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0;
		}
	}

	&__item+&__item {
		margin-top: 15rem;
	}

	&__item--loc {
		&::before {
			background-image: url('../img/svg/loc.svg');
			width: 22rem;
			height: 28rem;
			left: 6rem;
		}
	}

	&__item--tel {
		&::before {
			background-image: url('../img/svg/tel.svg');
			width: 23rem;
			height: 23rem;
			left: 5rem;
		}
	}

	&__item--mail {
		&::before {
			background-image: url('../img/svg/mail.svg');
			width: 32rem;
			height: 32rem;
			left: 0;
		}
	}
}