.intro {
	padding: 0 15rem;
	margin-top: 45rem;

	&__bg {
		max-width: 1670px;
		margin: 0 auto;
		background-color: $color-primary;
		border-radius: 25rem;
	}

	&__content {
		display: flex;
		align-items: center;
		padding: 40rem 0 15rem;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__left {}

	&__title {
		font-weight: 700;
		font-size: 60rem;
		line-height: 76rem;
		color: #fff;
		max-width: 657rem;

		@media (max-width: 991px) {
			font-size: 48rem;
			line-height: 64rem;
		}
	}

	&__desc {
		font-size: 20rem;
		line-height: 145%;
		color: #fff;
		margin-top: 25rem;
		max-width: 460rem;

		@media (max-width: 991px) {
			font-size: 18rem;
		}
	}

	&__iphone {
		margin-left: 175rem;
		width: 434rem;
		height: 672rem;

		@media (max-width: 991px) {
			width: 350rem;
			height: 538rem;
			margin: 60rem auto 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}