.repair {
	margin-top: 60rem;
	padding: 120px 0;
	background-color: #F8F8F8;

	@media (max-width: 991px) {
		margin-top: 70rem;
		padding: 60px 0;
	}

	&__content {
		@media (min-width: 992px) {
			display: flex;
			align-items: center;
		}
	}

	&__image {
		height: 597rem;

		@media (min-width: 992px) {
			flex: 0 0 54%;
		}

		@media (max-width: 991px) {
			width: 418rem;
			height: 345rem;
			margin: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		@media (min-width: 992px) {
			flex: 0 0 45%;
			margin-left: 30rem;
		}

		@media (max-width: 991px) {
			margin-top: 50rem;
		}
	}

	&__desc {
		margin-top: 45rem;
		font-size: 20rem;
		line-height: 145%;
		color: #2A2D31;

		@media (max-width: 991px) {
			font-size: 18rem;
			margin-top: 20rem;
		}
	}
}