.header {
	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-top: 30rem;

		@media (max-width: 991px) {
			padding-top: 15rem;
		}
	}

	&__logo {
		width: 119rem;
		height: 51rem;
		position: relative;
		z-index: 10;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__lang {
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			justify-content: center;
			margin-top: 20rem;
		}

		img {
			width: 27rem;
			height: 27rem;
		}

		&:hover span {
			color: #4E4E4E;
		}

		span {
			font-weight: 500;
			font-size: 18rem;
			margin-left: 8rem;
			color: $color-primary;
			transition: all 0.3s ease;
		}
	}
}

.menu {
	&__body {
		@media (max-width: 991px) {
			background-color: #fff;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 110rem 20rem 20rem;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 70rem;
				z-index: 10;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-bottom: 1rem solid #ccc;

				&:first-child {
					border-top: 1rem solid #ccc;
				}
			}
		}

		&>li+li {
			@media (min-width: 992px) {
				margin-left: 70rem;
			}
		}
	}

	&__link {
		color: #4E4E4E;
		font-weight: 500;

		@media (max-width: 991px) {
			display: block;
			padding: 20rem 0;
		}
	}

	&__icon {
		@media (max-width: 991px) {
			z-index: 5;
			display: block;
			position: relative;
			width: 35rem;
			height: 20rem;
			cursor: pointer;

			span,
			&:before,
			&:after {
				left: 0;
				position: absolute;
				height: 10%;
				width: 100%;
				transition: all 0.3s ease 0s;
				background-color: $color-primary;
				border-radius: 10rem;
			}

			&:before,
			&:after {
				content: "";
			}

			&:before {
				top: 0;
			}

			&:after {
				bottom: 0;
			}

			span {
				top: 50%;
				transform: scale(1) translate(0, -50%);
			}

			&.active span {
				transform: scale(0) translate(0, -50%);
			}

			&.active:before {
				top: 50%;
				transform: rotate(-45deg) translate(0, -50%);
			}

			&.active:after {
				bottom: 50%;
				transform: rotate(45deg) translate(0, 50%);
			}
		}
	}
}