.advantage {
	padding-top: 150rem;

	@media (max-width: 991px){
		padding-top: 75rem;
	}

	&__content {
		display: flex;
		justify-content: space-between;
		margin-top: 60rem;

		@media (max-width: 991px) {
			flex-wrap: wrap;
			margin-top: 30rem;
		}
	}

	&__item {
		flex: 0 0 23.5%;
		border: 1rem solid #EEF0EF;
		box-shadow: 0 28rem 61rem -17rem #EBECEE;
		border-radius: 8rem;
		padding: 40rem 20rem;

		@media (max-width: 991px) {
			flex: 0 0 48%;
			margin-bottom: 20rem;
		}

		&:nth-child(1) {
			.advantage__img {
				width: 97rem;
			}
		}

		&:nth-child(2) {
			.advantage__img {
				width: 98rem;
			}
		}

		&:nth-child(3) {
			.advantage__img {
				width: 112rem;
			}
		}

		&:nth-child(4) {
			background-color: #E6F1FC;

			.advantage__img {
				width: 119rem;
			}
		}
	}

	&__img {
		height: 95rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__subtitle {
		font-weight: 700;
		font-size: 18rem;
		line-height: 23rem;
		margin-top: 25rem;
		min-height: 45rem;
	}

	&__text {
		font-size: 18rem;
		line-height: 145%;
		color: #485467;
		margin-top: 10rem;
	}

	&__buttons {
		margin-top: 15rem;
	}

	&__button {
		img {
			width: 56rem;
			height: 56rem;
		}
	}

	&__button+&__button {
		margin-left: 15rem;
	}
}